import ContactProvider,{ContactContext} from '../utils/ContactProvider'

export default () => (
    <ContactProvider>
        <ContactContext.Consumer>
            {({contacts}) => {
                contacts.map((contact, i) => {
                    return (
                        <p>{contact.email}</p>
                    )
                }) 
            }}
        </ContactContext.Consumer>
    </ContactProvider>
)